

















































import Vue from "vue";
import Component from "vue-class-component";
import { PostModel, RequestData } from "@/x-vue/services/interfaces";
import { AppService } from "@/service/app.service";
import PostTitleMeta from "@/x-vue/components/forum/PostTitleMeta.vue";
import store from "@/store";

@Component({
  components: {
    PostTitleMeta,
  },
})
export default class PostList extends Vue {
  app = AppService.instance;

  loadingPosts = false;

  total = 0;
  limit = 5;
  noOfPages = 10;
  currentPage = "1";

  options: RequestData = {};

  get category(): string {
    return this.$route.params.category;
  }

  get posts(): PostModel[] {
    return store.state.posts;
  }

  linkGen(pageNum: number): string {
    return pageNum === 1 ? "?" : `?page=${pageNum}`;
  }

  onPageChanged(page: number): void {
    // console.log("page changed", page);
    this.options.page = page;
    this.loadPosts();
  }

  async mounted(): Promise<void> {
    // console.log("PostList::mounted(); ", this.$route.params);
    this.options.categoryId = this.$route.params.category;
    this.options.userIdx = this.$route.params.userIdx;
    this.options.limit = this.limit;
    this.options.page = 1;
    this.options.comments = 1;
    this.loadPosts();

    this.currentPage = this.$route.query.page as string;

    try {
      this.total = await this.app.api.postCount(this.options);
      this.noOfPages = Math.ceil(this.total / this.limit);
    } catch (e) {
      this.app.error(e);
    }
  }

  async loadPosts(): Promise<void> {
    // console.log(this.options);
    this.loadingPosts = true;
    try {
      store.state.posts = await this.app.api.postSearch(this.options);
      // console.log(store.state.posts);
      this.loadingPosts = false;
    } catch (error) {
      this.app.error(error);
      this.loadingPosts = false;
    }
  }
}
